	},
	onLoad(e) {
		let pic = uni.getStorageSync('picShow');
		let token = uni.getStorageSync('token');
		let password = uni.getStorageSync('password')
		this.picShow = pic;
		this.token = token;
		if (token) {
			let egCode = uni.getStorageSync('egCode')
			this.egCode = egCode;
			if (uni.getStorageSync('egCode') == '' || uni.getStorageSync('egCode') == undefined || uni.getStorageSync('egCode') ==
				null) {

			} else {
				uni.navigateTo({
					url: '/pages/charging/charging'
				})
			}
		}
	},
	onShow() {
		let token = uni.getStorageSync('token');
		if (token) {
			let egCode = uni.getStorageSync('egCode')
			this.egCode = egCode;
		}
	},
	methods: {
		// 获取授权信息
		getUserInfo(e) {
			console.log(e)
		},
		// 显示充电状态
		showCharging() {
			let self = this;
			if (uni.getStorageSync('token') == '' || uni.getStorageSync('token') == undefined || uni.getStorageSync('token') ==
				null) {
				uni.reLaunch({
					url: '/pages/login/login'
				})
				return;
			}
			uni.navigateTo({
				url: '/pages/charging/charging'
			})
		},
		//模态框隐藏和显示
		showCode() {
			let self = this;
			if (uni.getStorageSync('token') == '' || uni.getStorageSync('token') == undefined || uni.getStorageSync('token') ==
				null) {
				uni.reLaunch({
					url: '/pages/login/login'
				})
				return;
			}
			if (this.picShow == 1) {
				// 允许从相机和相册扫码
				uni.scanCode({
					success: function(res) {
						console.log('条码类型：' + res.scanType);
						console.log('条码内容：' + res.result);
						let data = {
							custName: uni.getStorageSync('custName'),
							ewmUrl: res.result
						}
						uni.showLoading({
							title: '加载中',
							mask: true
						})
						// 扫码成功开始充电
						utlis.http("POST", 'appChargeStartQRCodeHj', data, function(
							// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/appChargeStartQRCodeHj', data, function(
							res) {
							if (res.status == "success") {
								uni.setStorageSync('egCode', res.egCode)
								uni.setStorageSync('connectorID', res.connectorID)
								uni.hideLoading()
								uni.showToast({
									title: res.msg,
									duration: 3000,
									mask: true,
									icon: 'success'
								})
								setTimeout(() => {
									uni.navigateTo({
										url: '/pages/charging/charging'
									})
								}, 3000)
							} else {
								uni.hideLoading()
								uni.showToast({
									title: res.msg,
									duration: 3000,
									mask: true,
									icon: 'none'
								})
							}
						})
					}
				})
			} else {
				this.boxopa = 1;
				this.zindex = 99;
				this.back = 1;
			}
		},
		// 模态框隐藏，调取扫码
		hideCode() {
			let self = this;
			this.boxopa = 0;
			this.zindex = 0;
			this.back = 0;
			// 允许从相机和相册扫码
			uni.scanCode({
				success: function(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					let data = {
						custName: uni.getStorageSync('custName'),
						ewmUrl: res.result
					}
					uni.showLoading({
						title: '加载中',
						mask: true
					})
					// 扫码成功开始充电
					utlis.http("POST", 'appChargeStartQRCodeHj', data, function(
						// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/appChargeStartQRCodeHj', data, function(
						res) {
						if (res.status == "success") {
							uni.setStorageSync('egCode', res.egCode)
							uni.setStorageSync('connectorID', res.connectorID)
							uni.hideLoading()
							uni.showToast({
								title: res.msg,
								duration: 3000,
								mask: true,
								icon: 'success'
							})
							setTimeout(() => {
								uni.navigateTo({
									url: '/pages/charging/charging'
								})
							}, 3000)
						} else {
							uni.hideLoading()
							uni.showToast({
								title: res.msg,
								duration: 3000,
								mask: true,
								icon: 'none'
							})
						}
					})
				}
			});
		},
		showPic() {
			if (this.picShow == 0) {
				this.picShow = 1;
				uni.setStorageSync("picShow", this.picShow)
			} else {
				this.picShow = 0
				uni.setStorageSync("picShow", this.picShow)
			}
		},
		// 轮播图
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		change(e) {
			this.current = e.detail.current
		},
		change1(e) {
			this.current1 = e.detail.current
		},
		showPower() {
			uni.navigateTo({
				url: '/pages/power/power'
			});
		},
		showPowerDetail() {
			if (uni.getStorageSync('token') == '' || uni.getStorageSync('token') == undefined || uni.getStorageSync('token') ==
				null) {
